.how-to-buy{

    

    &__info{
        
        @include onGrid;
        row-gap: 12px;
        font-family: "Manrope", sans-serif;
        font-weight: 600;
        padding: 20px;

        border-radius: 20px;
        color: #fff;
    }

    &__text{
        font-size: 12px;
        @include hover(transform,scale(1.05));
        border-radius: 20px;
        padding: 10px;
        grid-column: 1/-1;

        
        background-color: $dark-brown-c;
        margin: 0;
        
        @include onTablet{
          grid-column: 1/-1;
          font-size: 18px;
        }

        @include onDesktop{
          grid-column: 1/8;  
        }

        &--fourth{
            text-align: left;
        }
    }



    &__indent{
        opacity: 0.8;
        color: $text-brown-c;
        grid-column: 1/-1;
        font-size: 16px;
        text-align-last: left;
        margin: 0;

         @include onDesktop{
            font-size: 20px;
         }

    }

    &__addition{
        
        opacity: 0.5;
        font-size: 13px;
        grid-column: span 4;
        display: flex;
        justify-content: left;
        margin-bottom: 10px;

        @include onDesktop{
          grid-column: 1/9;  
        }
    }
}