.section-title{
    text-align: center;
  margin: 0;
  font-weight: 400;
  font-family: "Manrope", sans-serif;
  font-size: 48px;
  line-height: 85%;
  letter-spacing: -5%;
  text-transform: uppercase;
  margin-bottom: 24px;
  color: #b3704a;

  @include onTablet{
    font-size: 56px;

  }

  @include onDesktop{
    font-size: 72px;

  }

  &--order{
    text-align: left;
  }

}