/* #region nav*/

.nav{


  &__list{
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 24px;

  }

  &__link{
    font-family: "Manrope", sans-serif;
    font-size: 48px;
    line-height: 150%;
    letter-spacing: -1%;
    text-decoration: none;
    color: #fff;;
    position: relative;
    &::after{
      transform-origin: left;
      display: block;
      content: '';
      position: absolute;
      bottom: -8px;
      height: 1px;
      width: 100%;
      background-color: #fff;
      transition: transform 0.3s;
      transform: scale(0);

    }
    &:hover::after{
      content: '';
      transform: scale(1);
    }

  }


    &__contacts{
      text-decoration: none;
      display: flex;
      align-items: center;
      justify-content: center;
    }
}

/* #endregion */
