/* #region header*/

.header{
    border-bottom-left-radius: 40px;
    border-bottom-right-radius: 40px;
    width: 100%;
    min-height: 100vh;
    box-sizing: border-box;
        
    background-image: url(/src/images/image/bgimage.png);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center bottom;

    @include onTablet{
        background-position: center;
    }

  &__content{
    padding: 32px 16px 40px;


  }

  &__title{
    font-weight: 400;
    font-size: 64px;
    line-height: 85%;
    letter-spacing: -5%;
    text-align: center;
    text-transform: uppercase;
    color: #f3f1ee;

    @include onTablet{
      font-size: 80px;
    }
  }

  &__learn-more{

    width: 288px;
    height: 56px;
    border: 2px solid $c-orange;
    border-radius: 32px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    margin: 0 auto;


    @include onTablet{
      margin-top: 80px;
    }

    &--text{
      color: #fff;
      font-weight: 500;
      font-style: Medium;
      font-size: 16px;
      line-height: 100%;
      letter-spacing: 2%;
      text-transform: uppercase;

    }
  }




  .header__bottom{


    &-describe{
        
        font-family: "Manrope", sans-serif;
        color: #f3f1ee;
      font-size: 20px;
      line-height: 150%;
      letter-spacing: -1%;
      text-align: center;
      padding-block: 24px;
      margin: 0;

      @include onTablet{
            font-size: 32px;
        }
    }


  }

}

/* #endregion */
