.top-bar{
  font-family: "Manrope", sans-serif;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 16px 20px;

  &__logo{
    font-size: 34px;
    text-decoration: none;
    color: #fff;
    display: flex;
    @include hover(transform,scale(1.05));
  }

    &__nav{
        display: none;

        @include onDesktop{
            display: flex;
        }

    }
    &__nav-list{
        display: flex;
        justify-content: center;
        gap: 20px;
        margin: 0;
        padding: 0;
        list-style: none;
    }

    &__nav-link{
        color: $c-white;
        text-decoration: none;
        position: relative;
        &::after{
      transform-origin: left;
      display: block;
      content: '';
      position: absolute;
      bottom: -8px;
      height: 1px;
      width: 100%;
      background-color: #fff;
      transition: transform 0.3s;
      transform: scale(0);

    }
    &:hover::after{
      content: '';
      transform: scale(1);
    }
    }
}
