.how-to-order{
    font-family: "Manrope", sans-serif;


    &__wrapper{
        @include onGrid;    
        row-gap: 18px;
    }

    &__indent{
        grid-column: 1/-1;
        display: flex;
        flex-direction: column;
        row-gap: 8px;
        
    }

    &__title{
        margin: 0;
        opacity: 0.8;
        color: $text-brown-c;
        font-size: 16px;
        text-align-last: left;

         @include onDesktop{
            font-size: 20px;
         }
    }

    &__description{
        color: $c-white;
        margin: 0;
        opacity: 0.5;
        font-size: 13px;
        grid-column: span 4;
        display: flex;
        justify-content: left;
        margin-bottom: 16px;

        @include onDesktop{
          grid-column: 1/9;  
        }
    }

    &__steps{
        
        display: grid;
        
    }

    &__step{
        position: relative;
        grid-column: 1/-1;
        @include hover(transform,scale(1.05));
        background-color: $dark-brown-c;
        padding: 8px;
        border-radius: 12px;
        &-title{
            color: #B87333;
            margin: 0;
            font-size: 18px;
            font-weight: 900;

            @include onTablet{
               font-size: 24px; 
            }

             @include onDesktop{
                font-size: 28px;
             }
        }

        &-text{
            margin: 0;
            font-size: 14px;
            color: $c-white;
            font-weight: 400;

            @include onDesktop{
               font-size: 20px;
            }
        }

        &--1{
        @include onTablet{
                grid-column: 1/4;
            }

            @include onDesktop{
          grid-column: 1/7;  
        }
        }
        &--2{
            @include onTablet{
                grid-column: 1/4;
                
            }

            @include onDesktop{
          grid-column: 1/7;  
        }
        }
        &--3{
            @include onTablet{
                grid-column: 4/-1;
                
                bottom: 130px;
            }
            
                
            @include onDesktop{
          grid-column: 7/-1;  
        }
            
        }
        &--4{
            @include onTablet{
                grid-column: 4/-1;
                
                bottom: 130px;
            }
          
            @include onDesktop{
                grid-column: 7/-1;  
            }
        }

    }

    &__link{
        color: $c-white;
        text-decoration: none;
        position: relative;

        &::after{
            content: '';
            position: absolute;
            bottom: -2px;
            width: 100%;
            display: block;
            height: 1px;
            background-color: $c-white;
            left: 0;
            right: 0;
        }

    }
}