.contacts{
    font-family: "Manrope", sans-serif;
    &__wrapper{
            @include onGrid;
    }

    &__info{
        border-radius: 10px;
        grid-column: 1/-1;
        background-color: $dark-brown-c;
        padding: 10px;

        @include onTablet{
            grid-column: 1/-1;
        }
        
         @include onDesktop{
            grid-column: 1/7;
         }
    }

    &__phone{
        margin-bottom: 20px;
        color: $c-white;
        display: inline-block;
        height: 50px;
        line-height: 50px;
        font-size: 16px;
        text-decoration: none;
            position: relative;
        &::after{
        transform-origin: left;
        display: block;
        content: '';
        position: absolute;
        bottom: -8px;
        height: 1px;
        width: 100%;
        background-color: #fff;
        transition: transform 0.3s;
        transform: scale(0);

    }
    &:hover::after{
      content: '';
      transform: scale(1);
    }

        @include onTablet{
            font-size: 22px;
        }
    }

    &__telegram{
        gap: 5px;
        display: flex;
        align-items: center;

        font-size: 16px;


        @include onTablet{
            font-size: 24px;
        }
    
        
    }

    &__text-telegram{
        color: $c-white;
        text-decoration: none;
                position: relative;
        &::after{
        transform-origin: left;
        display: block;
        content: '';
        position: absolute;
        bottom: -8px;
        height: 1px;
        width: 100%;
        background-color: #fff;
        transition: transform 0.3s;
        transform: scale(0);

    }
    &:hover::after{
      content: '';
      transform: scale(1);
    }
    }
}