.product{

    @include hover(transform,scale(1.05));

    grid-column: 1/-1;

    @include onTablet{
        grid-column: span 3;
    }
    
    @include onDesktop{
        grid-column: span 4;
    }


    
    &__wrapper{
      border-radius: 15px;
      background: $dark-brown-c;
  
      
    }
    
    &__content{
    @include onGrid;

    row-gap: 56px;
    
    @media (max-width: 768px) {
        .product__arrow {
            display: none;
        }
    }


  }

  &__media {
    position: relative;
    width: 100%;
  }

  &__slider {
    scrollbar-width: none;
    display: flex;
    overflow-x: auto;
    scroll-snap-type: x mandatory;
    -webkit-overflow-scrolling: touch;
    gap: 0;

    &::-webkit-scrollbar {
  display: none;
    }
  }

  &__photo {
    border-radius: 15px;
    flex: 0 0 100%;
    width: 100%;
    scroll-snap-align: start;
    object-fit: cover;
  
    &--spil{
        
        @media (max-width: 576px) {
            max-height: 500px;
        }
    }
}

  .product__dots {
  position: absolute;
  bottom: 12px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  gap: 6px;
}

.product__dots span {
  width: 6px;
  height: 6px;
  background: rgba(255,255,255,0.4);
  border-radius: 50%;
}

    &__arrow {
    
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 36px;
    height: 36px;
    border-radius: 50%;
    background: rgba(26,23,20,0.75);
    border: 1px solid rgba(255,255,255,0.15);
    cursor: pointer;
    z-index: 3;

    display: flex;
    align-items: center;
    justify-content: center;

    opacity: 0;
    transition: opacity 0.3s ease, transform 0.3s ease;

    &::before {
      content: '';
      width: 8px;
      height: 8px;
      border-top: 2px solid #fff;
      border-right: 2px solid #fff;
    }
  }

  &__arrow--prev {
    left: 10px;

    &::before {
      transform: rotate(-135deg);
    }
  }

  &__arrow--next {
    right: 10px;

    &::before {
      transform: rotate(45deg);
    }
  }

  &__media:hover &__arrow {
    opacity: 1;
  }

  .product__dots span.is-active {
  background: #eacc60;
  transform: scale(1.2);
}

&__info{
     font-family: "Manrope", sans-serif;
    text-align: center;
    font-weight: 500;
    
  color: #fff;
}

&__title{
    margin: 0;
    padding-top: 12px;
    position: relative;

    &::before{
        content:'';
        display: block;
        position: absolute;
        top: 5px;
        height: 1px;
        right: 0;
        left: 0;
        background-color: rgba(255,255,255,0.08);
    }
}

&__describe{
    margin: 0;
    padding-block: 8px;
}
}

