@mixin hover($_property, $_toValue) {
  transition: #{$_property} 0.3s;
  &:hover {
    #{$_property}: $_toValue;
  }
}

@mixin onTablet {
  @media (min-width: $tablet-min-width) {
    @content
  }
}

@mixin onDesktop{
  @media (min-width: $desktop-min-width) {
    @content
  }
}

@mixin onBigDesktop{
  @media (min-width: $desktop-BIG-min-width) {
    @content
  }
}

@mixin constentPaddingInline() {
  padding-inline: 16px;

  @include onTablet{
    padding-inline: 44px;
  }

  @include onDesktop{
    max-width: 1600px;
    margin-inline: auto;
  }



}

.container{
  @include constentPaddingInline()
}

@mixin onGrid{
    display: grid;
    grid-template-columns: repeat(2,1fr);

        @include onTablet{
        grid-template-columns: repeat(6,1fr);
      
      column-gap: 24px;
    }

    @include onDesktop{
      justify-items: stretch;
      grid-template-columns: repeat(12,1fr);
      column-gap: 24px;
    }
}