/* #region page*/

    *{
        scroll-behavior: smooth;
    }

  .page{
    font-family: Lora, serif;
    font-optical-sizing: auto;
    font-weight: 700;
    font-style: normal;
    scroll-behavior: smooth;

    &__body {
      margin: 0;
      min-width: 320px;
      background-color: #24201d;
    }
  }

.page__menu{
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
    z-index: 4;

  transition: all 0.3s;
  transform: translateX(-100%);
  opacity: 0;
  pointer-events: none;

  &:target{
    opacity: 1;
    pointer-events: all;
    transform: translateX(0);
  }

  

}

/* #endregion */
