/* #region icon*/

.icon{
  display: block;
  height: $icon-size;
  width: $icon-size;
  background-repeat: no-repeat;
  background-size: contain;

  @include hover(transform,scale(1.05));

  @include onTablet {
    height: $icon-size-b;
    width: $icon-size-b;
  }


  &--menu{

          @include onDesktop{
    display: none;
  }

    background-image: url(/src/images/icons/icon-burger-menu.svg);

    &:hover{
        background-image: url(/src/images/icons/icon-burger-menu-hover.svg);
    }
}

  &--close{
    background-image: url(/src/images/icons/icon-close.svg);
  }


  


  &--telegram{
    background-image: url(/src/images/icons/telegram-icon.svg);
  }
}

/* #endregion */
